.create-formik {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	margin-top: 25px;
  
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding: 5px;
		width: -webkit-fill-available;
		&__label {
			width: -webkit-fill-available;
		}

		&__field {
			// width: -webkit-fill-available;
			display: block;
			width: 100%;
			height: calc(2.25rem + 2px);
			padding: 0.375rem 0.75rem;
			font-family: inherit;
			font-size: 1rem;
			font-weight: 400;
			line-height: 1.5;
			color: #212529;
			background-color: #fff;
			background-clip: padding-box;
			border: 1px solid #bdbdbd;
			border-radius: 0.25rem;
			transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
		}

		&__field-uncorrect {
			// width: -webkit-fill-available;
			display: block;
			width: 100%;
			height: calc(2.25rem + 2px);
			padding: 0.375rem 0.75rem;
			font-family: inherit;
			font-size: 1rem;
			font-weight: 400;
			line-height: 1.5;
			color: #212529;
			background-color: #fff;
			background-clip: padding-box;
			border: 1px solid #e07676;
			border-radius: 0.25rem;
			outline: #e07676 auto 1px;
			transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
		}

		&__error {
			color: #e07676;
		}

		&__optional {
			color: gray;
		}
	}
}
